export default {
	data() {
		return {
			playerurl: '',
			desc: '',
			title:'',
			index:-1
		}
	},
	onLoad(option) {
		console.log(option)
		if(option){
			this.index = option.focusid
		}
			uni.request({
				url: '../../static/data/focus.json',
				method: 'GET',
				data: {},
				success: (res) => {
					// console.log(res)
					this.playerurl = res.data.data[this.index].playurl
					this.title = res.data.data[this.index].title
					this.desc = res.data.data[this.index].desc
				},
				fail: (err) => "网络连接超时",
				complete: () => {}
			})			
	},
	methods: {
		
	}
}
